/*
 *        expression ::= additive
 *          additive ::= multiplicative ( ('+' | '-') multiplicative )*
 *    multiplicative ::= unary ( ('*' | '/') unary )*
 *             unary ::= ( '+' | '-' )? primary
 *           primary ::= number | word | quoted_string | '(' expression ')'
 *            number ::= digit_sequence ('.' digit_sequence)?
 *    digit_sequence ::= digit+
 *              word ::= not_operator_char+
 *     quoted_string ::= '"' (char | escape_sequence)* '"'
 *              char ::= any character except '"' and '\'
 *   escape_sequence ::= '\\' any_character
 * not_operator_char ::= any character except '+', '-', '*', '/', '(', ')' and '"'
 *             digit ::= '0' .. '9'
 *     any_character ::= any printable character
 */

export const isExpression = input => input?.match(/[+\-*/]/) && !isQuotedString(input);
export const isQuotedString = input => input?.match(/^".*"$/);
export const extractText = input => input?.match(/((?<=")[^"\\]*(?:\\.[^"\\]*)*(?=")|[a-zA-Z_][a-zA-Z0-9_]*)/g) ?? [];

export const parse = async (input, evaluator) => {
    const regexes = {
        whitespace: /(?<whitespace>\s+)/,
        operator: /(?<operator>[+\-*/])/,
        groupStart: /(?<groupStart>\()/,
        groupEnd: /(?<groupEnd>\))/,
        quotedString: /"(?<quotedString>[^"\\]*(?:\\.[^"\\]*)*)"/,
        number: /(?<number>[0-9]+(?:\.[0-9]+)?(?=[^0-9]|$))/,
        string: /(?<string>[^\\+\-*/()"]*[^\\+\-*/() ]+)/,
    };

    const tokenRegex = new RegExp(
        Object.values(regexes)
            .map(re => re.source)
            .join('|'),
        'g'
    );

    // /(?<whitespace>\s+)|(?<operator>[+\-*/])|(?<groupStart>\()|(?<groupEnd>\))|"(?<quotedString>[^"\\]*(?:\\.[^"\\]*)*)"|(?<number>[0-9]+(?:\.[0-9]+)?(?=[^0-9]|$))|(?<string>[^\\+\-*/()"]*[^\\+\-*/() ]+)/g;

    const tokenize = input =>
        [...(input.matchAll(tokenRegex) ?? {})].flatMap(match =>
            Object.entries(match?.groups ?? {})
                .filter(([, val]) => val != null)
                .map(([type, val]) => ({ type, val }))
        );

    const tokens = tokenize(input);
    let pos = 0;

    const nextToken = () => tokens[pos++];
    const peekToken = () => tokens[pos];

    const skipWhitespace = () => {
        while (peekToken()?.type === 'whitespace') {
            nextToken();
        }
    };

    const parsePrimary = async () => {
        skipWhitespace();
        const token = nextToken();
        switch (token.type) {
            case 'groupStart':
                const exprVal = await parseAdditive();
                skipWhitespace();
                const groupEnd = nextToken();
                if (groupEnd?.type !== 'groupEnd') {
                    throw new Error('Expected closing parenthesis');
                }
                return exprVal;
            case 'quotedString':
                return await evaluator.lookup(token.val.replace(/\\(.)/g, '$1'));
            case 'number':
                return await parseFloat(token.val);
            case 'string':
                return await evaluator.lookup(token.val);
            default:
                throw new Error(`Unexpected token: ${token.val}`);
        }
    };

    const parseUnary = async () => {
        skipWhitespace();
        const token = peekToken();
        if (token && token.type === 'operator' && (token.val === '+' || token.val === '-')) {
            pos++;
            const operandVal = await parseUnary();
            return token.val === '+' ? operandVal : evaluator.neg(operandVal);
        }
        return await parsePrimary();
    };

    const parseMultiplicative = async () => {
        skipWhitespace();
        let leftVal = await parseUnary();
        while (true) {
            skipWhitespace();
            const token = peekToken();
            if (token && token.type === 'operator' && (token.val === '*' || token.val === '/')) {
                pos++;
                const rightVal = await parseUnary();
                leftVal = token.val === '*' ? evaluator.mul(leftVal, rightVal) : evaluator.div(leftVal, rightVal);
            } else {
                return leftVal;
            }
        }
    };

    const parseAdditive = async () => {
        skipWhitespace();
        let leftVal = await parseMultiplicative();
        while (true) {
            skipWhitespace();
            const token = peekToken();
            if (token && token.type === 'operator' && (token.val === '+' || token.val === '-')) {
                pos++;
                const rightVal = await parseMultiplicative();
                leftVal = token.val === '+' ? evaluator.add(leftVal, rightVal) : evaluator.sub(leftVal, rightVal);
            } else {
                return leftVal;
            }
        }
    };

    return parseAdditive().then(tensor => tensor?.array());
};
