import { easePolyOut, interpolateNumber } from 'd3';

export const degToRad = deg => (deg * Math.PI) / 180;
export const radToDeg = rad => (rad * 180) / Math.PI;

/**
 * Clamps a given value between a lower and upper bound.
 * @param {number} value - The value to clamp.
 * @param {number} low - The lower bound.
 * @param {number} high - The upper bound.
 * @returns {number} - The clamped value.
 */
export const clamp = (value, low, high) => Math.max(low, Math.min(high, value));

/**
 * Returns the first non-NULL and non-NaN value from a list of values.
 * @param {...any} values - The list of values to search.
 * @returns {any} - The first non-NULL and non-NaN value, or undefined if not found.
 */
export const firstNonNaN = (...values) => values.find(v => v != null && !isNaN(v));

/**
 * Creates a timed interpolator function that calls a given interpolator with a clamped progress value.
 * @param {function} interpolator - The interpolator function to call.
 * @param {number} duration - The duration of the interpolation in milliseconds.
 * @param {boolean} [repeat] - Whether the interpolation should repeat after completion.
 * @param {number} [start] - The starting time of the interpolation, defaults to the time the first value is returned
 * @returns {function} - The timed interpolator function.
 */
export const timedInterpolator = (interpolator, duration, repeat, start) => t =>
    interpolator(t ?? clamp(((Date.now() - (start ??= Date.now())) % (repeat ? duration : Infinity)) / duration, 0, 1));

/**
 * Transposes a 2D array (matrix) by swapping its rows and columns.
 * @param {Array<Array>} array - The 2D array to be transposed.
 * @returns {Array<Array>} - The transposed 2D array.
 */
export const transpose = array => array[0]?.map((_, i) => array.map(row => row[i])) ?? [];

/**
 * Calculates the extents (min and max values) of each column in an array of vectors.
 * @param {Array<Array>} vectors - The array of vectors to calculate the extents for.
 * @returns {Object} - An object containing two arrays: "min" with the minimum values
 * and "max" with the maximum values for each column.
 */
export const extents = vectors =>
    transpose(vectors).reduce(
        ({ min, max }, column) => {
            min.push(Math.min(...column));
            max.push(Math.max(...column));
            return { min, max };
        },
        { min: [], max: [] }
    );

/**
 * A utility function that creates a timed interpolator with an easing function.
 * @param {Function} interpolator - The interpolator function to apply the easing function to.
 * @param {Function} fn - A function to be called with the eased value.
 * @param {Object} [options] - An object containing optional configuration values.
 * @param {number} [options.duration] - The duration of the interpolation in milliseconds, defaults to 750.
 * @param {Function} [options.ease] - The easing function to apply, defaults to easePolyOut.
 * @returns {Promise} - A promise that resolves after duration milliseconds
 */
export const decay = (interpolator, fn, { duration = 750, ease = easePolyOut } = {}) => {
    const ti = timedInterpolator(t => fn(interpolator(ease(t))), duration);
    // mark the beginning of duration, subsequent calls without t will be relative to now
    ti(0);
    // make subsequent calls 30 times each second
    const interval = setInterval(ti, 1000 / 30);

    const future = new Future();

    // schedule interval to end after duration
    setTimeout(_ => {
        clearInterval(interval);
        // mark the end
        ti(1);
        future.resolve();
    }, duration);

    return future.promise;
};

/**
 * Creates a scale interpolator that calculates the scale factor to apply.
 * @param {number} to - The target scale value.
 * @returns {Function} - A function that can be called with a time parameter to calculate the scale factor.
 */
export const interpolateScale = to => {
    let lastCall = 0;
    return t => {
        const result = Math.pow(to, t) / Math.pow(to, lastCall);
        lastCall = t;
        return result;
    };
};

/**
 * Creates an interpolator that returns the delta between interpolated values. Unlike most interpolators,
 * this one must maintain state between calls. Regardless of the order of calls, the returned value will
 * be the difference between the last value returned by interpolator and this one. This means repeated
 * calls to the returned delta interpolator with the same normalized time value t will yield different
 * results, depending on how it was last called.
 * @param {Function} interpolator - The base interpolator function.
 * @returns {Function} - A function that can be called with a time parameter to calculate the delta between interpolated values.
 */
export const interpolateDelta = (from, to) => {
    let last;
    const interpolator = interpolateNumber(from, to);
    return t => {
        const value = interpolator(t);
        const delta = last == null ? 0 : value - last;
        last = value;
        return delta;
    };
};

/**
 * Creates a rotation interpolator that calculates the rotation delta in radians.
 * @param {number} radians - The rotation value in radians.
 * @returns {Function} - A function that can be called with a time parameter to calculate the rotation delta.
 */
export const interpolateRotation = radians => interpolateDelta(interpolateNumber(0, radians));

/**
 * Rounds a number to a fixed precision of 4 decimal places.
 * @param {number} num - The number to round.
 * @returns {number} - The rounded number, or the original value if num is not a number.
 */
export const fix = num => +(num?.toFixed(2) ?? num);

export class FutureAbortedError extends Error {}

/**
 * Represents a Future that can be used to manage multiple async operations.
 * @class
 */
export class Future {
    done;
    promises = [];
    name;
    reject;
    resolve;

    /**
     * Creates a new Future instance.
     * @param {Object} [options] - An object containing optional configuration values.
     * @param {AbortSignal} [options.signal] - An optional AbortSignal to listen for abort events.
     * @param {boolean} [options.repeat] - Whether the future should repeat after completion, defaults to false.
     * @param {string} [options.name] - An optional name for the future, used for debugging purposes.
     */
    constructor({ signal, repeat = false, name = '' } = {}) {
        this.done = !repeat;
        this.name = name;
        signal?.on('abort', _ => this.abort());
        this.repeat();
    }

    get promise() {
        return this.promises.shift();
    }

    abort() {
        this.done = true;
        this.reject(new FutureAbortedError(`Future ${this.name ? `${this.name} ` : ''}aborted`));
    }

    async *[Symbol.asyncIterator]() {
        do {
            yield* this.promises;
            this.promises.length = 0;
        } while (!this.done);
    }

    repeat() {
        this.promises.push(
            new Promise((resolve, reject) => {
                this.resolve = arg => {
                    this.done || this.repeat();
                    resolve(arg);
                };
                this.reject = arg => {
                    this.done || this.repeat();
                    reject(arg);
                };
            })
        );
    }
}

/**
 * Rate limit a function by limiting the number of calls made within a specified duration.
 * Excess calls will be discarded or queued and executed as the rate limit allows based on the discard flag.
 *
 * @param {Function} fn - The function to rate limit.
 * @param {number} calls - The number of calls allowed within the specified duration.
 * @param {number} duration - The duration (in milliseconds) over which the calls are made.
 * @param {boolean} [discard=false] - If true, discard excess calls; otherwise, queue and execute them when the rate limit permits.
 * @returns {Function} - A rate-limited version of the provided function.
 */
export const rateLimit = (fn, calls, duration, discard = false) => {
    let recentCalls = 0;
    const outstandingCalls = [];

    return (...args) => {
        // If the rate limit has been reached
        if (recentCalls === calls) {
            // If discard is false, queue the excess call
            if (!discard) {
                outstandingCalls.push(args);
            }
            return;
        }

        // Execute the function with the provided arguments
        fn(...args);
        ++recentCalls;

        // Function to execute queued calls when the rate limit permits
        const timeout = () => {
            // Execute next queued call if any
            if (outstandingCalls.length) {
                const args = outstandingCalls.shift();
                fn(...args);
                setTimeout(timeout, duration);
                return;
            }
            --recentCalls;
        };

        // Schedule the timeout to decrement recentCalls and execute queued calls if needed
        setTimeout(timeout, duration);
    };
};
