import { Directive, PartType, directive } from 'lit/async-directive.js';
import { noChange } from 'lit';

class EventsDirective extends Directive {
    /**
     * Stores the ClassInfo object applied to a given AttributePart.
     * Used to unset existing values when a new ClassInfo object is applied.
     */
    constructor(partInfo) {
        super(partInfo);
        this.partInfo = partInfo;
        if (partInfo.type !== PartType.ELEMENT) {
            throw new Error('`events()` can only be used in an element.');
        }
        this.listeners = new Map();
    }

    setValue(eventMap) {
        this.detachListeners();

        const wrap = arrowFn =>
            function (...args) {
                arrowFn.bind(this)(...args);
            }.bind(this.partInfo.ELEMENT);

        this.listeners = {};
        Object.entries(eventMap).forEach(([type, listener]) => (this.listeners[type] = wrap(listener)));

        this.attachListeners();
    }

    reconnected() {
        this.connect();
    }

    disconnected() {
        this.disconnect();
    }

    update(_part, [{ eventMap }]) {
        if (eventMap) {
            this.setValue(eventMap);
        }
    }

    attachListeners() {
        this.detachListeners();
        for (const [type, handler] of Object.entries(this.listeners)) {
            this.partInfo.element.addEventListener(type, handler);
        }
    }

    detachListeners() {
        for (const [type, handler] of Object.entries(this.listeners)) {
            this.partInfo.element.removeEventListener(type, handler);
        }
    }

    render() {
        return noChange;
    }

    connect() {
        this.attachListeners();
    }

    disconnect() {
        this.detachListeners();
    }
}

export const events = directive(EventsDirective);
